import { Component, ViewChild, ElementRef, OnInit } from '@angular/core';

import { faArrowUp, faGlobeAmericas, faPaperclip, faVideo } from '@fortawesome/free-solid-svg-icons';
import { faFacebookMessenger, faLinkedin, faInstagram, faWhatsappSquare } from '@fortawesome/free-brands-svg-icons';
import { Router, NavigationEnd } from '@angular/router';
