export function courseReducer(state: CourseComplete = course, action: fromCourseAction.actions) {

    const courseComplete = {...state};

    if (action.type === fromCourseAction.GUARDAR) {
        return action.payload;
    } else if (action.type === fromCourseAction.VIDEO_ACTUAL) {
        courseComplete.actualVideo = action.payload;
        return courseComplete;
    } else {
        return state;
    }

}
