import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NotFoundComponent } from './feature/default/not-found/not-found.component';
import { FigmaComponent } from './feature/post/components/posts/figma/figma.component';
import { PayuComponent } from './feature/post/components/posts/payu/payu.component';

const routes: Routes = [
  { path: '', loadChildren: () => import('./feature/home/home.module').then(m => m.HomeModule) },
  { path: 'home', loadChildren: () => import('./feature/home/home.module').then(m => m.HomeModule) },
  { path: 'post', loadChildren: () => import('./feature/post/post.module').then(m => m.PostModule) },
  { path: 'payu', component: PayuComponent },
  { path: 'figma', component: FigmaComponent },
  { path: 'course', loadChildren: () => import('./feature/course/course.module').then(m => m.CourseModule) },
  { path: '**', component: NotFoundComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
