import { Action } from '@ngrx/store';
import { Video } from '../model/video';
import { CourseComplete } from '../model/courseComplete';

export const GUARDAR = '[Config] - Guardar';
export const VIDEO_ACTUAL = '[Config] - Actualizar Video Actual';

export class GuardarAction implements Action {
    readonly type = GUARDAR;
    constructor(public payload: CourseComplete) {}
}

export class ActualizarVideoActual implements Action {
    readonly type = VIDEO_ACTUAL;
    constructor(public payload: Video) {}
}

export type actions = GuardarAction | ActualizarVideoActual;
